import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, NavigationExtras, Router} from '@angular/router';
import {ToastController} from '@ionic/angular';
import {CartItem} from 'src/app/client1/models/cart-item.model';
import {CartService} from 'src/app/client1/services/cart.service';
import {SubFood} from '../../../models/subFood.model';
import {SubFoodClientService} from '../../../services/sub-food-client.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.page.html',
  styleUrls: ['./detail.page.scss'],
})
export class DetailPage implements OnInit {
  id: number;
  subfood: SubFood;
  private idReceived: any;

  constructor(
    private activatedRoute: ActivatedRoute,
    private subFoodService: SubFoodClientService,
    private cartService: CartService,
    private toastController: ToastController,
    private router: Router,
    private route: ActivatedRoute,
  ) {
    this.id = +this.activatedRoute.snapshot.paramMap.get('id');

    this.route.queryParams.subscribe(params => {
      if (this.router.getCurrentNavigation().extras.state) {
        this.idReceived = this.router.getCurrentNavigation().extras.state.sendId;

      }
    });
  }
  ngOnInit() {
      // @ts-ignore
    //this.subfood = this.getFoodById(this.id);
    //console.log('====>',this.subfood);
    this.getFoodById(this.id);
    console.log('===>'+this.id);
    console.log('++++>'+this.idReceived);

  }
  getFoodById(id: number) {
    this.subFoodService.getSubFoodsById(this.id)
      .subscribe(
        data => {
          this.subfood = data;
          console.log('detail : ',data);
        },
        // eslint-disable-next-line @typescript-eslint/no-shadow
        error => {
          console.log(error);
        });
  }

  addItemToCart() {
    const cartitem: CartItem = {
      id: this.subfood.id,
      name: this.subfood.title,
      price: this.subfood.price,
      image: this.subfood.pic,
      quantity: 1,
    };

    this.cartService.addToCart(cartitem);
    this.showConfirmatioMsg(this.subfood.title);
   // this.router.navigate(['/client1/client/listing']);

  }

  async presentToast(message: string) {
    const toast = await this.toastController.create({
      message,
      duration: 2000, // 4000 ms (4 secondes)
      position: 'middle', // Position du toast (peut être 'top', 'middle' ou 'bottom' ou 'top')
      color: 'success'

    });
    toast.present();
  }

  showConfirmatioMsg(title: string) {

    this.presentToast(title +' a bien été ajouté');
  }

  goBack() {
    // this.router.navigate(['/client1/client/detail', this.idReceived]);
    this.router.navigate(['/client/list-sub-food', this.idReceived]);
  }
}
