import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {SubFood} from '../models/subFood.model';

//const baseUrl = 'http://192.168.0.246:8080/api/client/subfoods';
const baseUrl = 'https://oafish-orange-production.up.railway.app/api/client/subfoods';

//const baseUrl2 = 'http://192.168.0.246:8080/api/client/subfoods/list';
const baseUrl2 = 'https://oafish-orange-production.up.railway.app/api/client/subfoods/list';

@Injectable({
  providedIn: 'root'
})
export class SubFoodClientService {

  constructor(private http: HttpClient) {
  }

  getAllSubFoodByFood(foodId: number): Observable<SubFood[]> {
    return this.http.get<SubFood[]>(`${baseUrl}/all/${foodId}`);
  }
  getAll(): Observable<SubFood[]> {
    return this.http.get<SubFood[]>(baseUrl);
  }

  findByParentFood(food: any): Observable<SubFood[]> {
    return this.http.get<SubFood[]>(`${baseUrl2}?food=${food}`);
  }

  getSubFoodsById(id: number): Observable<SubFood> {
    // @ts-ignore
    return this.http.get(`${baseUrl}/${id}`);
  }

  getAllSubCommand(food: any): Observable<SubFood> {
    // @ts-ignore
    return this.http.get(`${baseUrl}/${food}`);
  }

  get(id: any): Observable<SubFood> {
    // @ts-ignore
    return this.http.get(`${baseUrl}/${id}`);
  }

  create(data: any): Observable<any> {
    return this.http.post(baseUrl, data);
  }

  update(id: any, data: any): Observable<any> {
    return this.http.put(`${baseUrl}/${id}`, data);
  }

  delete(id: any): Observable<any> {
    return this.http.delete(`${baseUrl}/${id}`);
  }

  deleteAll(): Observable<any> {
    return this.http.delete(baseUrl);
  }

  findByTitle(title: any): Observable<SubFood[]> {
    return this.http.get<SubFood[]>(`${baseUrl}?title=${title}`);
  }

  deleteFoodsByFoodValue(foodValue: number) {
    return this.http.delete(`${baseUrl}/deleteAllByParents/${foodValue}`);
  }


  getMaxPrice(foodValue: number): Observable<number> {
    return this.http.get<number>(`${baseUrl}/max-price/${foodValue}`);
  }

  getMinPrice(foodValue: number): Observable<number> {
    return this.http.get<number>(`${baseUrl}/min-price/${foodValue}`);
  }
}
